import { Link } from "react-router-dom";
import './header.css'
import MenuIcon from '@mui/icons-material/Menu';
import { useState } from "react";



const Header = () => {
    const [menuOpen, setMenuOpen] = useState(false)

    const toggleMenu = () => {
        setMenuOpen(!menuOpen)
    }

    const MobileMenu = () => {
            return (
                <div id="mobileNavOptions">
                    <a href="#skills" className="mobile-nav-link" onClick={toggleMenu}>Skills</a>
                    <a href="#about" className="mobile-nav-link" onClick={toggleMenu}>About</a>
                    <a href="#projects" className="mobile-nav-link" onClick={toggleMenu}>Projects</a>
                    <a href="#resume" className="mobile-nav-link" onClick={toggleMenu}>Resume</a>
                    <a href="#contact" className="mobile-nav-link" onClick={toggleMenu}>Contact</a>
                </div>
            )
    }

    return (
        <div>
            <div className="header">
                <a href="#home">
                    <img id="logo" src={require('../imgs/NS Logo 2@4x.png')} />
                </a>
                <div className="desktopNav">
                    <a href="#skills" className="nav-link">Skills</a>
                    <a href="#about" className="nav-link">About</a>
                    <a href="#projects" className="nav-link">Projects</a>
                    <a href="#resume" className="nav-link">Resume</a>
                    <a href="#contact" className="nav-link">Contact</a>
                </div>
                <div className="mobileNav" onClick={toggleMenu}>
                    <MenuIcon />
                </div>
            </div>
            {menuOpen ? <MobileMenu /> : null}
        </div>   
    )
}

export default Header;